import { AppEncounterService } from "@/services/app_encounter_service";
import { PrintoutService } from '@/services/printout_service';
import Prop from "@/apps/GLOBAL_APP/global_prop";
export class HtsArtInitiationService extends AppEncounterService {
  patientID: number
  printoutService: PrintoutService = new PrintoutService()
    
  constructor(patientID: number, providerID = -1) {
      super(patientID, 79, providerID);
      this.patientID = patientID;
  }

  async printHtsLinkageId(code: string) {
    await this.printoutService.printLbl(
      `/patients/${this.patientID}/labels/print_hts_linkage_code/${code}`
    );
  }

    async getHtsCode() {
      return (await this.getFirstValueText('HTC serial number'))||'Un-assigned'
    }
  
  validLinkID(linkID: string): Array<string> | null {
    const mapping: { [key: string]: number } = {
      A: 0,
      C: 1,
      H: 2,
      K: 3,
      L: 4,
      N: 5,
      P: 6,
      W: 7,
      X: 8,
      Y: 9,
    };

    const regex = /^(\d{1,4})-(\d{1,3})-(\d{1,2})-([A-CE-HK-NP-Y])$/;
    const match = regex.exec(linkID);

    if (!match) {
      return ["Invalid LinkID format"]
    }

    const bookNo = parseInt(match[1], 10);
    const pageNo = parseInt(match[2], 10);
    const rowNo = parseInt(match[3], 10);
    const checkDigit = match[4];

    // book no and row no should not have leading zeros
    if (match[1].length !== bookNo.toString().length || match[3].length !== rowNo.toString().length || match[2].length !== pageNo.toString().length) {
      return ["Invalid LinkID, book number and row number should not have leading zeros"];
    }

    // Validate book number, page number, and row number
    if (
      isNaN(bookNo) ||
      isNaN(pageNo) ||
      isNaN(rowNo) ||
      bookNo < 1 ||
      bookNo > 9999 ||
      pageNo < 1 ||
      pageNo > 999 ||
      rowNo < 1 ||
      rowNo > 99
    ) {
      return ["Invalid LinkID format on book, page, or row number"];
    }

    // Validate check digit
    // eslint-disable-next-line no-prototype-builtins
    if (!mapping.hasOwnProperty(checkDigit)) {
      return ["Invalid check digit"];
    }

    return null;
  }
}
