
import EncounterMixinVue from "@/views/EncounterMixin.vue";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent } from "vue";
import {
  Field,
  Option,
} from "@/components/Forms/FieldInterface";
import { IonPage } from "@ionic/vue";
import { HtsArtInitiationService } from "../../services/hts_art_initiation_service";
import { getFacilities } from "@/utils/HisFormHelpers/LocationFieldOptions";
import { toDate } from "@/utils/Strs";
import { uniq } from "lodash";

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as HtsArtInitiationService,
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsArtInitiationService(
            this.patientID,  this.providerID
          );
          this.fields = [
            this.referralForArtInitiation(),
            this.intendedArtSiteField(),
            this.artAppointmentDataField(),
            this.htsLinkageCodeField(),
            this.hiddenArtReferralOutcomeStatusField(),
          ];
        }
      },
      immediate: true,
    },
  },
  methods: {
    onFinish(formData: any, computedData: any) {
      this.saveFormEncounterAndProceed({
        encounterTypeID: this.service.encounterTypeID,
        formData,
        computedData,
      })
    },
    referralForArtInitiation(): Field {
      return {
        id: "is_referral",
        helpText: "Referral for ART (Re-)Initiation",
        type: FieldType.TT_YES_NO,
        isRequired: () => true,
        finalComputedValue: (v: any) => {
          return {
            obs: this.service.buildValueText("ART referral", v),
          };
        },
        options: () => {
          return [
            {
              label: "Do you want to refer client?",
              value: "",
              values: this.yesNoOptions(),
            },
          ];
        },
      };
    },
    intendedArtSiteField(): Field {
      return {
        id: "art_site",
        helpText: "Intended Site for ART",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: ({ label }: Option) => ({
          obs: this.service.buildValueText("Referral location", label),
        }),
        options: (_: any, filter = "") => getFacilities(filter),
        condition: (f: any) => f.is_referral === "Yes",
        defaultValue: () => HtsArtInitiationService.getLocationName(),
        config: {
          footerBtns: [
            {
              name: "Here",
              slot: "end",
              onClickComponentEvents: {
                setValue: () => HtsArtInitiationService.getLocationName()
              },
              onClick: () => null,
            },
          ],
          showKeyboard: true,
          isFilterDataViaApi: true,
        },
      };
    },
    artAppointmentDataField(): Field {
      return {
        id: "art_appointment_date",
        helpText: "Appointment Date for ART Referral",
        type: FieldType.TT_DATE_PICKER,
        isRequired: () => true,
        defaultValue: () => this.service.date,
        condition: (f: any) => f.is_referral === "Yes",
        finalComputedValue: (d: Option) => {
          return {
            obs: this.service.buildValueDate("Appointment date", `${d.value}`),
          };
        },
        config: {
          minDate: () => this.service.date,
          infoItems: (date: string) => {
            return [
              {
                label: "Set appointment date",
                value: toDate(date),
              },
            ];
          },
          hiddenFooterBtns: ["Clear"],
        },
      };
    },
    htsLinkageCodeField() {
      let invalidLinkageCode: string[] = []
      return {
        id: 'linkage_code',
        helpText: 'LINKAGE CODE',
        type: FieldType.TT_LINKAGE_CODE,
        isRequired: () => true,
        validation: (v: Option) => {
          if (!v?.other?.isValidLinkageCode) {
            invalidLinkageCode = uniq(invalidLinkageCode.concat(`${v.value}`))
            return ['Invalid Scanform linkage code']
          }
          return null
        },
        condition: (f: any) => f.is_referral === "Yes",
        finalComputedValue: (v: Option) => {
          return {
            obs: [
              this.service.buildValueText('HTC Serial number', `${v.value}`),
              ...invalidLinkageCode.map((id) => this.service.buildValueText('Invalid', id))
            ]
          }
        }
      }
    },
    hiddenArtReferralOutcomeStatusField() {
      return {
        id: "art_outcome_field",
        helpText: "Art outcome",
        type: FieldType.TT_HIDDEN,
        defaultValue: () => {
          return this.toOption("Unknown");
        },
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded(
              "Antiretroviral status or outcome",
              v.value
            ),
          };
        },
      };
    },
  },
});
